/*This stylesheet is for the animation of the main navigation items*/

.nav_outer{
	z-index: 2;
	top: 0;
	left: 0;
	position: absolute;
	width: 100%;
	height: 100%;
	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#212735+0,345f75+100 */
	background: #212735; /* Old browsers */ /* FF3.6-15 */
	background: -webkit-linear-gradient(bottom,  #212735 0%,#345f75 100%); /* Chrome10-25,Safari5.1-6 */
	background: -webkit-gradient(linear,  left bottom, left top,  from(#212735),to(#345f75));
	background: -o-linear-gradient(bottom,  #212735 0%,#345f75 100%);
	background: linear-gradient(to top,  #212735 0%,#345f75 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#212735', endColorstr='#345f75',GradientType=0 ); /* IE6-9 */
}

.icon{
	position: absolute;
	left: -60px;
}

.icon.one{
	top: 3%;
	-webkit-transition-duration: 1s;
	     -o-transition-duration: 1s;
	        transition-duration: 1s;
	-webkit-transform: translateX(0);
	    -ms-transform: translateX(0);
	        transform: translateX(0);
}

 #open-menu.icon.one{
	top: 3%;
	-webkit-transition-duration: 200ms;
	     -o-transition-duration: 200ms;
	        transition-duration: 200ms;
	-webkit-transform: translateX(75px);
	    -ms-transform: translateX(75px);
	        transform: translateX(75px);
	-webkit-transition-delay: 50ms;
	     -o-transition-delay: 50ms;
	        transition-delay: 50ms;
}

