.App{
  position: absolute;
  border-radius: 5px;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
}
/*This is the on off animation states for the main users page */
#wrapper{
  position: absolute;
  z-index: 4;
  border-radius: 8px;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  -webkit-transition-duration: 200ms;
       -o-transition-duration: 200ms;
          transition-duration: 200ms;
  -webkit-transform: translateX(0) scaleY(1);
      -ms-transform: translateX(0) scaleY(1);
          transform: translateX(0) scaleY(1);
}

#wrapper.open-menu {
  position: absolute;
  z-index: 4;
  border-radius: 8px;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  -webkit-transition-duration: 200ms;
       -o-transition-duration: 200ms;
          transition-duration: 200ms;
  -webkit-transform: translateX(90px) scaleY(0.95);
      -ms-transform: translateX(90px) scaleY(0.95);
          transform: translateX(90px) scaleY(0.95);
}


